import React from 'react';

const Recipe = ({recipe}) => {
    const {title,image,nutrition } = recipe;
   

    return (
    <div className="recipe"> 
        <h2>{title}</h2> 
        <img src={image} alt={title}/>
       
        <label>Percentage of Carb</label><h3>{nutrition.caloricBreakdown.percentCarbs}</h3>
        <label>Percentage of Fat</label><h3>{nutrition.caloricBreakdown.percentFat}</h3>
        <label>Percentage of Protein</label><h3>{nutrition.caloricBreakdown.percentProtein}</h3>
    
           
     </div>
    );

};

export default Recipe;


